.Drawer__Drawer--2Nzcl {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 80%;
  max-width: 300px;
  padding: 20px 10px;
  box-sizing: border-box;
  background: #fff;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: -webkit-transform 0.22s ease-in;
  transition: -webkit-transform 0.22s ease-in;
  transition: transform 0.22s ease-in;
  transition: transform 0.22s ease-in, -webkit-transform 0.22s ease-in;
  z-index: 90;
}

.Drawer__Drawer--2Nzcl.Drawer__close--3eUiV {
  -webkit-transform: translateX(-300px);
          transform: translateX(-300px);
}

.Drawer__Drawer--2Nzcl ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Drawer__Drawer--2Nzcl ul li {
  margin-bottom: 15px;
}

.Drawer__Drawer--2Nzcl ul li a {
  color: #363d54;
  font-size: 30px;
  text-decoration: none;
  background-color: #fff;
  position: relative;
  padding: 0 20px 10px 20px;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

.Drawer__Drawer--2Nzcl ul li a:hover,
.Drawer__active--WxScd {
  opacity: 0.7;
}
